import React, { useState } from 'react';
import { ReactComponent as Logo } from '../../assets/LOGHI/LOGO BIANCO.svg';

const MENU_ITEMS = ['slider', 'news', 'percorso', 'tab'];

const Header = () => {
    const [menuOpen, setMenuOpen] = useState(false);

    const onClickMenuHandler = () => {
        setMenuOpen(!menuOpen);
    };

    return (
        <header className={`header${menuOpen ? ' header--menu-open' : ''}`}>
            <button className="header__burger-menu" onClick={onClickMenuHandler}>
                <div />
                <div />
                <div />
            </button>
            <Logo />
            <nav className="header__menu">
                <ul>
                    {MENU_ITEMS.map((item) => (
                        <li key={item} className="header__menu-item" onClick={() => setMenuOpen(false)}>
                            <a href={`#${item}`}>{item}</a>
                        </li>
                    ))}
                </ul>
            </nav>
        </header>
    );
};

export default Header;
