const Timeline = () => {
    const [nodes, setNodes] = useState<Node[]>(DEFAULT_NODES);

    const onClickHandler = (nodeIndex: number) => {
        setNodes((prevState) => {
            const updatedNodes = [...prevState];
            updatedNodes.forEach((n, index) => {
                n.open = index === nodeIndex;
            });
            return updatedNodes;
        });
    };

    const getClass = (node: Node, index: number): string => {
        return [
            'timeline__node',
            node.open ? ' timeline__node--open' : '',
            `timeline__node--${node.color}`,
            index % 2 ? ' timeline__node--right' : '',
        ]
            .filter((cls) => cls)
            .join(' ');
    };

    return (
        <section className="timeline" id="percorso">
            <div className="container timeline__container">
                {nodes.map((node, index) => (
                    <div key={`node-${index}`} className={getClass(node, index)}>
                        <span className="timeline__node-icon-title">
                            <span className="timeline__node-title">
                                <div>{node.title}</div>
                            </span>
                        </span>
                        <button className="timeline__node-icon" onClick={() => onClickHandler(index)}>
                            <img src={node.icon} alt="icon" />
                        </button>
                        <div className="timeline__node-content">
                            <h3 className="timeline__node-title">{node.title}</h3>
                            <p className="timeline__node-paragraph">{node.content}</p>
                        </div>
                    </div>
                ))}
            </div>
        </section>
    );
};

export default Timeline;
