import { ReactComponent as ArrowLeft } from '../../assets/SLIDER/chevron sinistra.svg';
import { ReactComponent as ArrowRight } from '../../assets/SLIDER/chevron destra.svg';

const IMAGES = [
    {
        image: './IMAGES/slider-1.png',
        title: 'Lorem ipsum dolor sit amet, consectetuer adipiscing elit',
        content:
            'sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcorper.',
        ctaText: 'Call to action',
        link: '#',
    },
    {
        image: './IMAGES/slider-2.jpg',
        title: 'No nummy nibh: euismod tin',
        content:
            'sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcorper.',
        ctaText: 'Call to action',
        link: '#',
    },
    {
        image: './IMAGES/slider-3.jpg',
        title: 'Lorem ipsum dolor sit amet, consectetuer adipiscing elit',
        content:
            'sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcorper.',
        ctaText: 'Call to action',
        link: '#',
    },
    {
        image: './IMAGES/slider-4.jpg',
        title: 'No nummy nibh: euismod tin',
        content:
            'sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcorper.',
        ctaText: 'Call to action',
        link: '#',
    },
];

const Slider = () => {
    const [activeImage, setActiveImage] = useState(0);

    const onChangeSlideHandler = (index: number) => {
        let i = index;
        if (index >= IMAGES.length) {
            i = 0;
        } else if (index < 0) {
            i = IMAGES.length - 1;
        }
        setActiveImage(i);
    };

    const onClickHandler = (link: string) => {
        alert(link);
    };

    return (
        <section className="slider" id="slider">
            <button className="slider__arrow slider__arrow--prev" onClick={() => onChangeSlideHandler(activeImage - 1)}>
                <ArrowLeft />
            </button>
            <button className="slider__arrow slider__arrow--next" onClick={() => onChangeSlideHandler(activeImage + 1)}>
                <ArrowRight />
            </button>
            <div className="slider__buttons-container">
                {IMAGES.map((item, index) => (
                    <button
                        key={item.image}
                        onClick={() => onChangeSlideHandler(index)}
                        className={`slider__button${activeImage === index ? ' slider__button--active' : ''}`}
                    />
                ))}
            </div>
            {IMAGES.map((item, index) => (
                <div
                    key={item.image}
                    style={{ backgroundImage: `url(${item.image})` }}
                    className={`slider__slide${activeImage === index ? ' slider__slide--active' : ''}`}
                >
                    <div className="slider__text">
                        <h2>{item.title}</h2>
                        <p>{item.content}</p>
                        <button onClick={() => onClickHandler(item.link)}>{item.ctaText}</button>
                    </div>
                </div>
            ))}
        </section>
    );
};

export default Slider;
